const tabs = $('.info_tab .tab');
const contents = $('.info_tab .content');

tabs.each((index, tab) => {
  $(tab).on('click', () => {
    tabs.removeClass('active01');
    contents.removeClass('active01');

    $(tab).addClass('active01');
    contents.eq(index).addClass('active01');
  });
});

